<template>
    <view class="card" @click="onClick" :style="[customStyle]" :data-i="$attrs['data-i']">
        <image class="card-img" mode="aspectFill" :src="src" />
        <view class="text-wrap text-wrap-width" v-if="!!desc">
            <view class="title one-t">{{title}}</view>
            <view class="desc one-t">{{desc}}</view>
        </view>
        <view v-else class="text-wrap-width title more-t">{{title}}</view>
        <image class="card-icon" src="data:image/png;base64,"></image>
    </view>
</template>

<script>
    export default {
        props: {
            mode: {
                type: Boolean,
                default: false
            },
            src: String,
            title: String,
            desc: String,
            url: String,
            color: String,
            bgcolor: String,
            border: String
        },
        data () {
            return {

            }
        },
        computed: {
            customStyle () {
                return {
                    'background-color': this.bgColor || '#a4d0ff',
                    border: this.border || '1px solid #FFF',
                    color: this.color || '#000'
                }
            }
        },
        methods: {
            onClick (e) {
              if (this.url && this.url.trim().length > 6 && !this.mode) {
                  uni.navigateTo({ url: this.url ,
                    animationType: 'pop-in',
                    animationDuration: 200})
              }
              this.$emit('click', e)
            }
        }
    }
</script>

<style lang="scss">
    .one-t {
    	overflow: hidden;
    	white-space: nowrap;
    	text-overflow: ellipsis;
    	transition: all linear 0.2s;
    }

    .more-t {
        overflow: hidden;
        text-overflow: ellipsis;
        word-break:break-all;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        transition: all linear 0.2s;
    }

    .card {

        width: 80%;
        margin: 10rpx auto;
        max-width: 700rpx;
        max-height: 140rpx;

        box-sizing: border-box;
        overflow: hidden;

        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 20rpx 0 20rpx 10rpx;
        border-radius: 12rpx;

        &-img {
            width: 96rpx;
            height: 96rpx;
            border-radius: 12rpx;
            flex: 0 0 96rpx;
        }

        &-icon {
            width: 30rpx;
            height: 96rpx;
        }

        .text-wrap {

            display: flex;
            flex-direction: column;
            justify-content: space-between;

            &-width {
                width: 72%;
            }
        }

        .title {
            font-weight: bold;
            font-size: 34rpx;
            line-height: 48rpx;
        }

        .desc {
            font-size: 27rpx;
            line-height: 37rpx;
        }
    }
</style>
